import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
export default {
	data(){
		return{
			globalUser:{},
			show_badge:true,
			badgeText:" ",
			notices:[]
		};
	},
	onShow() {
		var me = this;
		var globalUser = me.getGlobalUser("userInfos");
